  methods: {
    deleteTodo(id) {
      axios.delete(`https://jsonplaceholder.typicode.com/todos/${id}`)
        // eslint-disable-next-line no-unused-vars
        .then(res => this.todos = this.todos.filter(todo => todo.id !== id))
        // eslint-disable-next-line no-console
        .catch(err => console.log(err));
    },
    addTodo(newTodo) {
      const { title, completed } = newTodo;

      axios.post('https://jsonplaceholder.typicode.com/todos', {
        title,
        completed
      })
        .then(res => this.todos = [...this.todos, res.data])
        // eslint-disable-next-line no-console
        .catch(err => console.log(err));
    }
  },
  created() {
    // axios.get('https://jsonplaceholder.typicode.com/todos?_limit=5')
    //   .then(res => this.todos = res.data)
    //   // eslint-disable-next-line no-console
    //   .catch(err => console.log(err));
    this.todos = this.todos.map(snap=>{
      var url = "https://www.google.com/maps/search/?api=1&query="+snap.LATITUDE+","+snap.LONGITUDE;
      var data = {
          EMIS_NO:snap.EMIS_NO
          ,NAME: snap.NAME
          ,Address:snap.Address
          ,LONGITUDE:snap.LONGITUDE
          ,LATITUDE:snap.LATITUDE
          ,URL: url
        }
      return data
    })

  },
  computed: {
      filteredBlogs: function(){
          return this.todos.filter((todo) => {
              return todo.NAME.toUpperCase().match(this.search.toUpperCase()) || todo.Address.toUpperCase().match(this.search.toUpperCase());
          });
      }
  }
}
